// noui target

.noUi-target {
    background-color: $gray-100;
    border-radius: 0.5rem;
    border: none;
  }
  
  .noUi-horizontal {
    height: 8px;
  }
  
  .noUi-handle {
    border: transparent;
    border-radius: 100%;
    background: $white;
    cursor: default;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.18);
  }
  .noUi-handle:focus {
    outline: none;
  }
  
  .noUi-horizontal .noUi-handle {
    width: 18px;
    height: 18px;
    right: -13px;
    top: -6px;
  }
  .noUi-handle:after,
  .noUi-handle:before {
    display: none;
  }
  .noUi-connects {
    border-radius: 6px;
  }
  
  .noUi-connect {
    background: $primary;
  }
  .noUi-tooltip {
    border: none;
    padding: 0;
    background-color: transparent;
  }
  
  .noUi-tooltip {
    border: none;
    padding: 0;
    background-color: $primary;
    height: 30px;
    width: 30px;
    line-height: 30px;
    color: $white;
  }

